<template>
  <v-container>
    <!-- INTRO POP-UP -->
    <v-dialog v-model="firstIntroModal" width="80vw">
      <AdAccountsIntroPopup @closeModal="firstIntroModal = false" />
    </v-dialog>
    <!-- OPEN CAMPAIGN BLOCKS (SIMULATION) -->
    <v-row>
      <v-col cols="2">
        <v-btn @click="selected = 'accounts'">Accounts</v-btn>
      </v-col>
      <v-col cols="2">
        <v-btn @click="selected = 'campaigns'">Campaigns</v-btn>
      </v-col>
    </v-row>

    <!-- AD ACCOUNTS TABLE -->
    <v-row v-if="selected == 'accounts'">
      <v-col>
        <v-card>
          <v-toolbar dense dark color="primary">
            <v-toolbar-title>Ad Accounts</v-toolbar-title>
            <v-text-field
              v-model="adAccountSearch"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
              class="mx-12"
            />
          </v-toolbar>

          <v-card-text>
            <SelectionTable
              :inputData="accountSummary"
              :tableSelection="'accounts'"
              :search="adAccountSearch"
            />
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>


    <!-- CAMPAIGN SELECTION BLOCKS  -->
    <v-row v-if="selected == 'campaigns'">
      <v-col
        v-for="(campaign, index) in kpiOverviews"
        :key="'kpi-overview' + index"
      >
        <AdAccountsKpiOverview :campaignName="campaign" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from "vuex";

import AdAccountsIntroPopup from "../components/AdAccountsIntroPopup";
import AdAccountsKpiOverview from "../components/AdAccountsKpiOverview";
import SelectionTable from "../components/SelectionTable";

export default {
  name: "AdAccounts",
  components: {
    AdAccountsIntroPopup,
    AdAccountsKpiOverview,
    SelectionTable,
  },
  data() {
    return {
      adAccountSearch: "",
      firstIntroModal: false, // initial display of AdAccountsIntroPopup
      selected: "campaigns", // string: v-if display "accounts" or "campaigns" code
    };
  },
  computed: {
    ...mapState(["accountSummary", "campaignSummary"]),

    kpiOverviews() {

      let input = this.campaignSummary.slice(0, 2);
      let output = [];

      input.map((item) => {
        output.push(item.name);
      });

      return output;
    },
  },
};
</script>
