<template>
  <v-data-table
    dense
    :headers="headers"
    :items="inputData"
    :items-per-page="8"
    :footer-props="{ 'items-per-page-options': [5, 8, 10, 15, -1] }"
    :sort-by="'last_update'"
    :search="search"
    sort-desc
    must-sort
    @click:row="selectRow"
    class="elevation-0 account-row-pointer"
  />
</template>
    
<script>
export default {
  name: "SelectionTable",
  props: {
    inputData: Array,
    tableSelection: String,
    search: String,
  },
  computed: {

    // Takes an array with json objects, extracts all keys and removes duplicates
    headers() {
      let input = this.inputData;
      let uniqueKeys = [...new Set(input.flatMap((item) => Object.keys(item)))];
      let output = [];

      uniqueKeys.map((category) => {
        if (category == "name") {
          output.push({ text: category.split("_").join(" "), value: category, width: 440 });
        } else if (
          ["last_update"].includes(category)
        ) {
          output.push({
            text: category.split("_").join(" "),
            value: category,
            filterable: false,
            width: 124,
          });
        } else if (!["ad_account_id", "campaign_id"].includes(category)) {
          output.push({
            text: category.split("_").join(" "),
            value: category,
            filterable: false,
          });
        }
      });

      return output;
    },
  },
  methods: {
    selectRow(row) {
      if (this.tableSelection === "account") {
        this.selectAccount(row.ad_account_id);
        this.selectTable("accounts");
      }
      if (this.tableSelection === "campaign") {
        this.selectCampaign(row.campaign_id);
        this.openPage(row.campaign_id);
        this.selectTable("campaigns");
      }
    },

    openPage(campaignID) {
      this.$router.push({
        name: "ElasticCampaign",
        params: { accountID: this.selectedAccount, campaignID: campaignID },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.account-row-pointer:hover {
  cursor: pointer;
}
</style>
